import { createStore } from 'vuex';
import { createUserWithEmailAndPassword } from 'firebase/auth';
import { auth, usersCollection, addDoc } from '@/includes/firebase';

export default createStore({
  state: {
    authModalShow: false,
    authTabShow: false,
    userLoggedIn: false,
  },
  mutations: {
    toggleAuthModal: (state) => {
      state.authModalShow = !state.authModalShow;
    },
    toggleAuthTabSwitch: (state) => {
      state.authTabShow = !state.authTabShow;
    },
    toggleAuth: (state) => {
      state.userLoggedIn = !state.userLoggedIn;
    },
  },
  getters: {
    // authModalShow: (state) => state.authModalShow,  <= use mapGetters inside computed property
  },
  actions: {
    async register({ commit }, payload) {
      await createUserWithEmailAndPassword(
        auth, payload.email, payload.password,
      );

      await addDoc(usersCollection, {
        name: payload.name,
        email: payload.email,
        age: payload.age,
        country: payload.country,
        user_type: payload.user_type,
      });
      commit('toggleAuth');
    },
  },
});
